import { Component, OnInit } from '@angular/core';
// import { LiffService } from './services/liff.service';

declare var liff: any;
const SERVICE_UUID = 'c4dd444d-6d46-47de-8b24-c3b70fbf8b31';
// const LED_CHARACTERISTIC_UUID   = 'E9062E71-9E62-4BC6-B0D3-35CDCD9B027B';
// const BTN_CHARACTERISTIC_UUID   = '62FBD229-6EDD-4D1A-B554-5C4E1BB29169';
​
// PSDI Service UUID: Fixed value for Developer Trial
const PSDI_SERVICE_UUID         = 'E625601E-9E55-4597-A598-76018A0D293D';
const PSDI_CHARACTERISTIC_UUID  = '26E2B12B-85F0-4F3F-9FDD-91D114270E6E';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'LIFF Mock';
  messages: string;
  userProfile: any;
  statusBle: boolean;

  characteristic: any;

  constructor() {
    this.messages = '';
    this.statusBle = true;
  }

  ngOnInit() {
    liff.init(() => this.initLineLiff(), (error) => alert('31' + JSON.stringify(error)));
  }

  ngOnChange() {
    alert('ngOnChange');
    this.characteristic.addEventListerner('characteristicvaluechanged', (e) => {
      alert('ここには入ってこない');
      alert(e.target.value);
    });
  }

  async initLineLiff() {
    await liff.initPlugins(['bluetooth']);
    const device = await liff.bluetooth.requestDevice();
    const gatt = await device.gatt.connect();
    const service = await gatt.getPrimaryService(PSDI_SERVICE_UUID);
    this.characteristic = await service.getCharacteristic(PSDI_CHARACTERISTIC_UUID);
    this.characteristic.readValue().then((bufferData) => {
      alert(bufferData);
      const binaryData = String.fromCharCode.apply('', new Uint32Array(bufferData));
      alert('binaryData : ' + binaryData);
      // const dv = new DataView(bufferData);
      // const binaryData = dv.getUint32(4);
    });
    this.characteristic.addEventListerner('characteristicvaluechanged', (e) => {
      alert('ここには入ってこない');
      alert(e.target.value);
    });
    await this.characteristic.startNotifications();
  }
}
